@brand-primary: #223049;
@gray-base: #F1F5F7;
@gray-light: #819DA9;
@gray-lighter: #D3D5D8;

@flag-info: url(data:image/svg+xml;base64,);
@flag-default: url(data:image/svg+xml;base64,);

@logo-primary: url(data:image/svg+xml;base64,);
@logo-inverse: url(data:image/svg+xml;base64,);

@logo-full-primary: url(data:image/svg+xml;base64,);

@logo-full-inverse: url(data:image/svg+xml;base64,);


@brand-default-hover: #E2E6E8;
@brand-primary-hover: #429C30;
//@brand-success-hover: lighten(@brand-success, 10%);
@brand-info-hover: #008EC0;
@brand-warning-hover: #DF8701;
//@brand-danger-hover:

h1, h2, .btn {
    text-transform: uppercase;
}
h3, h4, h5, h6 {
    font-weight: 500;
}
h5, h6 {
    //color: @gray-light;
}

h1, .h1, h2, .h2, h3, .h3 {
    margin-top: 25px;
    margin-bottom: 3.5px;
}
h4, .h4, h5, .h5, h6, .h6 {
    margin-top: 18.5px;
    margin-bottom: 3.5px;
}


