.panel {
    box-shadow: 0px 1px 4px @gray-lighter;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.175);
}
.panel-default {
    border: 0px;
}

.table-bordered {
    border-radius: @border-radius-small;

    > thead {
        background-color: @gray-base;
    }

    > tbody {
        > tr > th {
            color: @brand-primary;
        }
    }

    > thead > tr > th,
    > tbody > tr > th,
    > tfoot > tr > th,
    > thead > tr > td,
    > tbody > tr > td,
    > tfoot > tr > td {
        border-left: none;
        border-right: none;
    }
}

.table {
    font-size: 12px;

    > thead > tr > th,
    > tbody > tr > th,
    > tfoot > tr > th,
    > thead > tr > td,
    > tbody > tr > td,
    > tfoot > tr > td {
        padding: 22px 16px 18px;
    }

    > thead > tr > th {
       border-bottom-width: 1px;
   }
}


.label {
    padding: 0.35em 0.6em 0.15em;
}
.badge {
    padding: 4px 7px 2px;
}
.breadcrumb {
    padding: 10px 15px 8px;
}
.page-header {
    padding-bottom: 3px;
}

.nav > li > a,
.panel-heading {
    padding: 11px 15px 9px;
}
.navbar-nav > li > a,
.navbar-brand  {
    padding-top: 20.5px;
    padding-bottom: 18.5px;
}
.navbar-text {
    margin-top: 20.5px;
    margin-bottom: 18.5px;
}
.pager li > a,
.pager li > span {
    padding: 6px 14px 4px;
}

body {
    font-weight: 300;
}
strong {
    color: @gray-dark;
}
address {
    margin-bottom: 11px;
}
footer {
    background-color: @gray-base;
}
blockquote footer {
    background-color: transparent;
}

@media (max-width: @screen-xs-max) {
    footer {
        position: relative;
    }

    .navbar .navbar-brand {
        margin-left: 15px !important;
    }
}

.well,
.form-control,
.btn:active,
.btn.active {
    box-shadow: none;
}

.btn-default .glyphicon {
    color: @brand-info;
}
.btn-default[disabled] .glyphicon {
    color: @gray-light;
}

.btn-default {
    &:hover,
    &:focus {
        background-color: @brand-default-hover;
        border-color: @gray-lighter;
    }
    &:active:hover {
        background-color: @gray-lighter;
        border-color: @gray-lighter;
    }
}
.btn-primary {
    &:hover,
    &:focus {
        background-color: @brand-primary-hover;
        border-color: @brand-primary-hover;
    }
    &:active:hover,
    &.active:hover {
        background-color: darken(@brand-primary-hover, 10%);
        border-color: darken(@brand-primary-hover, 10%);
    }
}
.btn-success {
    &:hover,
    &:focus {
        background-color: @brand-success;
        border-color: @brand-success;
        color: #FFF;
    }
    &:active,
    &.active,
    &:active:hover,
    &.active:hover {
        background-color: @brand-primary-hover;
        border-color: @brand-primary-hover;
        color: #FFF;
    }
}
.btn-info {
    &:hover,
    &:focus {
        background-color: @brand-info-hover;
        border-color: @brand-info-hover;
    }
    &:active:hover,
    &.active:hover {
        background-color: darken(@brand-info-hover, 10%);
        border-color: darken(@brand-info-hover, 10%);
    }
}
.btn-warning {
    &:hover,
    &:focus {
        background-color: @brand-info;
        border-color: @brand-info;
        color: #FFF;
    }
    &:active,
    &.active,
    &:active:hover,
    &.active:hover {
        background-color: @brand-info-hover;
        border-color: @brand-info-hover;
        color: #FFF;
    }
}
.btn-link {
    &:hover,
    &:focus {
        border: 1px solid @brand-info;
        border-radius: @border-radius-base;
        color: @brand-info;
        text-decoration: none;
    }
    &:active,
    &.active {
        color: #FFF;
        background-color: @brand-info;
        border-color: @brand-info;
    }
}

.btn,
.form-control,
.input-group-addon
.pagination > li > a,
.pagination > li > span {
    padding: 7px 12px 5px;

    .caret {
        margin-top: -3px;
    }
}
.btn-xs,
.btn-group-xs > .btn,
.input-group-xs .form-control,
.input-group-xs .input-group-addon {
    padding: 2px 5px 0px;
}

.btn-sm,
.btn-group-sm > .btn,
.form-group-sm .btn,
.input-group-sm .form-control,
.input-group-sm .input-group-addon,
.pagination-sm > li > a,
.pagination-sm > li > span {
    padding: 6px 10px 4px;
}


.btn-lg,
.btn-group-lg > .btn,
.input-group-lg .form-control,
.input-group-lg .input-group-addon,
.pagination-lg > li > a,
.pagination-lg > li > span {
    padding: 11px 16px 9px;
}

.form-control-feedback {
    font-size: 16px;
}
.form-horizontal .control-label {
    padding-top: 8px;
}
.form-control-static {
    padding-top: 8px;
    padding-bottom: 6px;
}

.input-sm .form-control-feedback,
.input-group-sm .form-control-feedback,
.form-group-sm .form-control-feedback {
    font-size: 16px;
    top: -2px !important;
}
.input-lg .form-control-feedback,
.input-group-lg .form-control-feedback,
.form-group-lg .form-control-feedback {
    font-size: 22px;
}
.input-lg .input-group-addon,
.input-group-lg .input-group-addon,
.form-group-lg .input-group-addon, {
    font-size: 18px;
}

.form-group-sm {
    .form-control {
        padding: 6px 10px 4px;
    }

    .btn{
        padding: 5px 10px 2px;
    }
}

.form-group-lg {
    .form-control,
    .btn  {
        padding: 11px 16px 9px;
        height: 46px; // Fix webkit/gecko difference
    }
}
.input-group-addon {
    color: @gray-light;
}

.jumbotron,
.bg-primary {
    h1, h2, h3, h4, h5, h6 {
        color: #FFF;
        font-weight: 800;
    }
}
.jumbotron {
    h1 {
        font-size: 42px;
    }
}

.bg-warning {
    background-color: @gray-base;
}


.logo {
    background-image: @logo-full-primary;
    background-repeat: no-repeat;
    background-position: 50% 50%;

    &.logo-primary {
        background-image: @logo-primary;
    }
    &.logo-inverse {
        background-image: @logo-inverse;
    }
    &.logo-full-primary {
        background-image: @logo-full-primary;
    }
}

.flag {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-image: @flag-default;

    &.flag-default {
        background-image: @flag-default;
    }
    &.flag-info {
        background-image: @flag-info;
    }
}

.navbar:not(.navbar-inverse) {
    background-color: @brand-primary;
}
.navbar > .container .navbar-brand,
.navbar > .container-fluid .navbar-brand {
    margin-left: 0;
}

.navbar .navbar-brand,
.navbar-inverse .navbar-brand {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    margin-top: 8px;
    margin-right: 10px;
    height: 16px;
    width: 118px;

    color: transparent;

    &:hover,
    &:focus {
        color: transparent;
    }
}
.navbar .navbar-brand {
    background-image: @logo-full-inverse;
}
.navbar-inverse .navbar-brand {
    background-image: @logo-full-primary;
}

.label {
    padding-top: 0.25em;
    position: relative;
    top: -3px;

    .label-default {
        color: @gray-light;
        background-color: @brand-default-hover;
    }
}

.btn .badge {
    top: -2px;
}
.badge {
    top: -1px;
    position: relative;
}
.list-group-item > .badge {
    top: 0;
}

.alert {
    .close {
        opacity: 0.7;
    }
}
.alert-dismissable .close,
.alert-dismissible .close {
    top: -1px;
}

.modal-content {
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
    border: none;
}
.modal-header {
    padding: 17px 15px 13px;

    .close {
        margin-top: 4px;
    }
}
.popover {
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
}
.popover-title {
    padding: 9px 14px 7px;
}
